section {
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    
}

@font-face {
    src: url("font/Galmuri11.ttf");
    font-family: regular;
}

body {
    font-family: regular;
    padding: 0;
    margin: 0;
}

.crew-img {
    position: absolute;
    left: 50%;
    top: 250px;
    transform: translateX(-50%);
    text-align: center;
    margin-left: -6px;
}

.title {
    position: absolute;
    left: 50%;
    text-align: center;
    transform: translate(-50%);
    font-size: 30px;
    font-weight: bold;
    top: 440px;
}

.title img {
    height: 100%;
    opacity: 0;
    animation: title-start 0.35s ease 0.7s forwards;
}

.title {
    position: absolute;
    height: 35px;
    text-align: center;
}

.download-btn {
    transform-origin: center left;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    top: 530px;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    border-radius: 10px;
    border: 2px solid #4ec81b;
    width: 200px;
    height: 30px;
    padding: 10px;
    color: black;
    transition: all 0.2s ease;
    opacity: 0;
    animation: button-start 0.3s ease 1.15s forwards;
    cursor: pointer;
}

.download-btn:hover {
    background: linear-gradient(45deg, #43b716 30%, #4ec81b 50%);
    color: white;
    transform: scale(0.98) translateX(-50%);;
}

.crew-img img {
    position: absolute;
    image-rendering: pixelated;
    height: 150px;
    transform: scale(0);
    animation-name: start;
    animation-duration: 0.3s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
}

.crew-img .hiu {
    position: absolute;
    left: -50px;
    z-index: 10;
}

.crew-img .sebi {
    top: -25px;
    left: -145px;
    height: 140px;
    z-index: 9;
    animation-delay: 0.2s;
}

.crew-img .saehyeon {
    top: -30px;
    left: -10px;
    height: 150px;
    z-index: 9;
    animation-delay: 0.2s;
}

.crew-img .dummy {
    top: -70px;
    left: 70px;
    z-index: 8;
    animation-delay: 0.4s;
}

.latter {
    position: fixed;
    width: 100%;
    z-index: 999;
}

.latter>div {
    position: fixed;
    background: black;
    height: 70px;
    width: 110%;
    box-shadow: 0 0 10px gray;
}

.latter>div.bottom {
    bottom: 0;
}

@keyframes start {
    0% {
        transform: scale(0);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes title-start {
    from {
        opacity: 0;
        transform: scale(1.2);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes button-start {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}